body {
	z-index: -10;
}
.app {
	position: absolute;
	width: 100%;
	height: 100%;
	background: url("../images/background.svg");
	display: flex;
	z-index: -5;
	overflow-x: hidden;
	overflow-y: hidden;
}

.main-title {
	font-family: "Quicksand", sans-serif;
	margin: 0 auto;
	position: absolute;
	width: 50vw;
	left: 25vw;
	color: white;
	text-align: center;
	height: fit-content;
	font-size: 5vw;
	z-index: 2;
	transform: translateX(0vw);
	transition: 1s;
}
.left {
	transform: translateX(-15vw);
	transition: 1s;
	transition-delay: 0.8s;
}
.info-card-list {
	position: absolute;
	width: 30vw;

	margin-left: 100vw;
	transition: all 1s;
	transition-delay: 0.5s;
}

.close-info-cards {
	background-image: url("../images/nvm.svg");
	position: absolute;
	left: 1vw;
	top: 1vw;
	height: 33px;
	width: 48px;
	z-index: 5;
	visibility: hidden;
	cursor: pointer;
}

.slide-out {
	transition: 1s;
	transition-delay: 0.5s;
	margin-left: calc(100vw - 30vw);
}

